import socket from "../SocketIO";
import eventManager from "./EventManager";


class Client {

    responseTime = 0;

    constructor() {
        this.uid = window.localStorage.getItem("uid") || "";
        this.key = window.localStorage.getItem("key") || "";
        this.authorized = false;
        this.response = {};
        eventManager.create("ready");
        eventManager.create("authorization_updated");
        this.join();
    }

    // -- POST
    /** @returns {Promise<{authorized: Boolean, e_username: String, e_password: String }>} */
    login(u = "", p = "", s = 1) {
        return this.asyncEmit("login", { u, p, s });
    }
    /** @returns {Promise<{authorized: Boolean, e_username: String, e_email: String, e_password: String }>} */
    register(u = "", e = "", p = "", t = 1, s = 1) {
        return this.asyncEmit("register", { u, e, p, t, s });
    }

    // -- GET

    /** @param {Number} refreshRate */
    ping(refreshRate = 2000) {
        let msTime = 0;
        let ping = () => { msTime = performance.now(); socket.emit("ping"); };
        socket.on("pong", () => { this.responseTime = performance.now() - msTime; setTimeout(ping, refreshRate); });
        ping();
    }
    join() {
        this.asyncEmit("join").then(() => eventManager.trigger("ready"));
    }
    /** @returns {Promise<{serverNumber: number, ready: boolean, ended: boolean, isFull: boolean, started: number, players: number }[]>}*/
    listServers() {
        return this.asyncEmit("listServers");
    }
    homePage() {
        return this.asyncEmit("homePage");
    }

    setUID(uid = "") {
        this.uid = uid;
        window.localStorage.setItem("uid", uid);
    }
    updateAuthorization(authorized = false, key = "") {
        this.authorized = authorized;
        this.key = key;
        window.localStorage.setItem("key", key);
        eventManager.trigger("authorization_updated");
    }
    setState(authorized = false, uid = "", key = "") {
        this.authorized = authorized;
        this.uid = uid;
        this.key = key;
        window.localStorage.setItem("uid", uid);
        window.localStorage.setItem("key", key);
        eventManager.trigger("authorization_updated");
    }

    getResponse(name = "") {
        return this.response[name + "_client"];
    }
    /** @param {String} name * @param {{}} data */
    asyncEmit(name, data = {}) {
        name = name + "_client";
        eventManager.create(name);
        socket.emit(name, { uid: this.uid, key: this.key, data });
        return eventManager.waitFor(name, () => this.response[name]);
    }
}

const client = new Client();

/** @param {String} name * @param {function({}): void} func */
function onResponse(name, func) {
    name = name + "_client";
    socket.on(name + "_response", response => {
        client.authorized = response.authorized;
        eventManager.trigger("authorization_updated");
        client.response[name] = response;
        func(response);
        eventManager.trigger(name);
    });
}

onResponse("homePage", () => { });

onResponse("listServers", () => { });

onResponse("join", response => client.setUID(response.uid));

onResponse("login", response => client.updateAuthorization(response.authorized, response.key));

export default client;