import React, { lazy, Suspense } from 'react';
import { Switch, Route } from 'react-router-dom';
import LinearProgress from '@material-ui/core/LinearProgress';
import "./Content.css";

const Home = lazy(() => import('./Home'));
const Login = lazy(() => import('./Login'));
const Register = lazy(() => import('./Register'));

function Progress() {
    return <LinearProgress style={{ position: "absolute", top: "0px", left: "0px", width: "100%" }} />
}

function Content() {
    let p = window.location.pathname, l = p.length;
    return <>
        <div className="content">
            <Suspense fallback={<Progress />}>
                <Switch>
                    <Route exact path={["/", "/Home"]}><Home /></Route>
                    <Route exact path="/Login"><Login /></Route>
                    <Route exact path="/Register"><Register /></Route>
                    <Route exact path="/Rules">التعليمات</Route>
                    {/* Auth */}
                    <Route exact path="/Village">خريطة القرية</Route>
                    <Route exact path="/Buildings">مركز القرية</Route>
                    <Route exact path="/Map">الخريطة</Route>
                    <Route exact path="/Statistics">الاحصائيات</Route>
                    <Route exact path="/Reports">التقارير</Route>
                    <Route exact path="/Messages">الرسائل</Route>
                    <Route exact path="/View">عرض القرية</Route>
                    <Route exact path="/Overview">نظرة عامة على القرى</Route>
                    <Route><br /><h3 className="NotFound">الصفحة التي طلبتها غير موجودة</h3></Route>
                </Switch>
            </Suspense>
        </div>
    </>
}

export default Content;